body {
  min-height: 100vh;
  min-width: 1200px;
  margin: 0;
  color: #333;
  line-height: 1.5;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  overflow: hidden;
}

code,
pre,
.grey-background {
  border: 1px solid #ddd;
  background: #f3f3f3;
  border-radius: 3px;
}

a {
  text-decoration: none;
  color: inherit;
}
a:active,
a:visited,
a:hover {
  color: inherit;
}

p {
  margin: 0;
}

h1 {
  font-size: 1.5em;
  -webkit-margin-before: 0.83em;
          margin-block-start: 0.83em;
  -webkit-margin-after: 0.83em;
          margin-block-end: 0.83em;
}

*,
:after,
:before {
  box-sizing: content-box;
}

textarea,
input {
  border: 1px solid #e7eaf1;
  border-radius: 3px;
  padding: 6px 12px;
}

input[type="password"]:focus,
input[type="text"]:focus,
input[type="email"]:focus,
textarea:focus {
  outline: none;
  border: 1px solid #206890;
  box-shadow: 0 0 4px #206890;
  -moz-box-shadow: 0 0 4px #206890;
  -webkit-box-shadow: 0 0 4px #206890;
}

input::-webkit-input-placeholder {
  color: #aaa;
}

input::-moz-placeholder {
  color: #aaa;
}

input:-ms-input-placeholder {
  color: #aaa;
}

input::-ms-input-placeholder {
  color: #aaa;
}

input::placeholder {
  color: #aaa;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

code {
  color: #d32f2f;
  font-size: 0.75rem;
  padding: 2px 3px;
  font-weight: normal !important;
  font-style: normal !important;
}

pre {
  font-size: 0.75rem;
  border-radius: 4px;
  padding: 9px 7px;
}

blockquote {
  padding-left: 0.9rem;
  border-left: 4px solid #ddd;
  margin: 10px 0;
}

button {
  padding: 0;
}

.btn {
  border-radius: 2px;
  background-color: #297fa3;
  font-size: 1.2rem;
  font-weight: 500;
  color: #fefefe;
  padding: 0 30px;
  cursor: pointer;
  display: inline-block;
  line-height: 30px;
  height: 40px;
  border: none;
  outline: none;
  border: 1px solid transparent;
  box-sizing: border-box;
}
.btn:hover {
  background: #368bc1;
  box-shadow: 0 0 2px #368bc1;
  -moz-box-shadow: 0 0 2px #368bc1;
  -webkit-box-shadow: 0 0 2px #368bc1;
  text-decoration: none;
}
.btn:active {
  background-color: #277aac;
  box-shadow: inset 0 0 10px #174a80;
  -moz-box-shadow: inset 0 0 10px #174a80;
  -webkit-box-shadow: inset 0 0 10px #174a80;
}

.btn.white-btn {
  background-color: white;
  color: #333;
  border: 1px solid #ddd;
  font-weight: 600;
  font-size: 1rem;
}

.btn.white-btn:hover,
.btn.white-btn:active {
  background-color: white;
  box-shadow: 0 0 1px #999;
  -moz-box-shadow: 0 0 1px #999;
  -webkit-box-shadow: 0 0 1px #999;
}

.btn.disabled-button {
  background: #aaa;
  box-shadow: 0 0 1px #999;
  -moz-box-shadow: 0 0 1px #999;
  -webkit-box-shadow: 0 0 1px #999;
  pointer-events: none;
}

.panel {
  background: #fff;
  overflow: hidden;
  border-radius: 6px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
}

.form-container {
