  color: #666;
}

.horizontal-divider {
  position: relative;
  padding: 20px 0;
  width: 100%;
  text-align: center;
  height: 20px;
}
.horizontal-divider span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: white;
  z-index: 100;
  padding: 0 20px;
  border-radius: 17px;
  font-weight: 600;
}

.horizontal-divider::after {
  content: "";
  width: 100%;
  position: absolute;
  height: 1px;
  top: 50%;
  background-color: #ddd;
}
.horizontal-divider::after {
  right: 0;
}

